/* ----- 共通ボタン ----- */
.button {
  width: 12.625rem;
  max-width: 100%;
  display: inline-block;
  padding: 0.9375rem 1rem;
  border: 0.0625rem solid #408F95;
  background: #408F95;
  color: #fff;
  font-size: 0.875rem;
  font-family: "Gotu", sans-serif;
  line-height: 1.5714285714;
  text-align: center;
  position: relative;
  z-index: 1;
  -webkit-tap-highlight-color: transparent;
}

.button > div {
  width: 12.625rem;
  height: 3.375rem;
  border: 0.0625rem solid #408F95;
  position: absolute;
  top: 0.25rem;
  left: 0.225rem;
  z-index: -5;
}

.button span {
  display: inline-block;
  padding-right: 3rem;
  position: relative;
}

.button span::first-letter {
  text-transform: capitalize;
}

.button span::before, .button span::after {
  content: "";
  position: absolute;
}

.button span::before {
  width: 0.625rem;
  height: 0.625rem;
  right: 0.0625rem;
  top: 50%;
  transform: translateY(-40%) rotate(45deg);
  border-top: 0.0625rem solid #fff;
}

.button span::after {
  width: 2.5rem;
  height: 0.0625rem;
  right: 0;
  top: 53%;
  transform: translateY(-40%);
  background: #fff;
}

.button::after {
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.3s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button--reversal {
  border: 0.0625rem solid #fff;
  background: #fff;
  color: #408F95;
  text-transform: capitalize;
}

.button--reversal > div {
  border: 0.0625rem solid #fff;
}

.button--reversal span {
  padding-right: 3.5625rem;
}

.button--reversal span::before {
  border-top: 0.0625rem solid #408F95;
}

.button--reversal span::after {
  background: #408F95;
}

.button--reversal::after {
  background: #408F95;
}

@media (hover: hover) {
  .button:hover {
    color: #408F95;
    opacity: 1;
  }
  .button:hover span::before {
    border-top: 0.0625rem solid #408F95;
  }
  .button:hover span::after {
    background: #408F95;
  }
  .button:hover::after {
    transform: scale(1, 1);
  }
  .button--reversal:hover {
    color: #fff;
  }
  .button--reversal:hover span::before {
    border-top: 0.0625rem solid #fff;
  }
  .button--reversal:hover span::after {
    background: #fff;
  }
  .button:active {
    transform: translate(0.25rem, 0.25rem);
  }
  .button:active div {
    opacity: 0;
  }
}
@media (hover: none) {
  .button:active {
    opacity: 1;
    transform: translate(0.25rem, 0.25rem);
  }
  .button:active div {
    opacity: 0;
  }
}
