/* ----- 送信ボタン ----- */
.submit-btn {
  width: 12.625rem;
  max-width: 100%;
  display: inline-block;
  border: 0.0625rem solid #408F95;
  background: #408F95;
  position: relative;
  z-index: 1;
  -webkit-tap-highlight-color: transparent;
  box-shadow: #fff 0.25rem 0.25rem 0 0, #408F95 0.25rem 0.25rem 0 0.0625rem;
  cursor: pointer;
}

.submit-btn span {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}

.submit-btn__input {
  color: #fff;
  font-size: 0.875rem;
  font-family: "Gotu", sans-serif;
  line-height: 1;
  text-transform: capitalize;
  width: 100%;
  height: 100%;
  padding: 1.2rem 3.5rem;
  text-align: left;
}

.submit-btn span::before, .submit-btn span::after {
  content: "";
  position: absolute;
}

.submit-btn span::before {
  width: 0.625rem;
  height: 0.625rem;
  right: 3.5625rem;
  top: 47%;
  transform: translateY(-47%) rotate(45deg);
  border-top: 0.0625rem solid #fff;
}

.submit-btn span::after {
  width: 2.5rem;
  height: 0.0625rem;
  right: 3.5rem;
  top: 48%;
  transform: translateY(-48%);
  background: #fff;
}

.submit-btn::after {
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.3s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

@media (hover: hover) {
  .submit-btn:hover .submit-btn__input {
    color: #408F95;
  }
  .submit-btn:hover span::before {
    border-top: 0.0625rem solid #408F95;
  }
  .submit-btn:hover span::after {
    background: #408F95;
  }
  .submit-btn:hover::after {
    transform: scale(1, 1);
  }
  .submit-btn:active {
    transform: translate(0.25rem, 0.25rem);
    box-shadow: none;
  }
}
@media (hover: none) {
  .submit-btn:active {
    transform: translate(0.25rem, 0.25rem);
    box-shadow: none;
  }
}
